import React, { Component } from "react";
import defaultBg from "../../node_modules/startbootstrap-clean-blog/img/home-bg.jpg";

export default class extends Component {
  render() {
    return (
      <header
        className="masthead"
        style={{
          backgroundImage: `url(${
            this.props.image ? this.props.image : defaultBg
          })`
        }}
      >
        <div className="overlay" />
        <div className="container">
          <div className="row">
            <div className="col-lg-8 col-md-10 mx-auto">
              <div className="site-heading">
                <h1>
                  {this.props.title !== undefined
                    ? this.props.title
                    : "Clean Blog"}
                </h1>
                <span className="subheading">
                  {this.props.subtitle !== undefined
                    ? this.props.subtitle
                    : "A new post"}
                </span>
              </div>
            </div>
          </div>
        </div>
      </header>
    );
  }
}
