import React, { Component } from "react";
import { Link } from "react-router-dom";
import Header from "./Header";
import { Loading, scrollTop } from "../Helpers";
import config from "../config";

export default class Post extends Component {
  state = {
    post: null
  };

  componentDidMount() {
    if (config.testing) {
      setTimeout(() => this.getPost(), 2000);
    } else {
      this.getPost();
    }
    scrollTop();
  }

  getPost() {
    fetch(
      `https://public-api.wordpress.com/wp/v2/sites/zubayr.home.blog/posts?slug=${
        this.props.match.params.slug
      }`
    )
      .then(res => res.json())
      .then(data => this.setState({ post: data[0] }));
  }

  render() {
    const { post } = this.state;

    if (post) {
      document.title = post.title.rendered;
      return (
        <React.Fragment>
          <Header
            title={post.title.rendered}
            subtitle={new Date(post.date).toDateString()}
            image={post.jetpack_featured_media_url}
          />
          <div className="container">
            <div className="row">
              <div className="col-lg-8 col-md-10 mx-auto">
                <div
                  dangerouslySetInnerHTML={{
                    __html: post.content.rendered
                  }}
                />
              </div>
            </div>
          </div>
        </React.Fragment>
      );
    }
    return <Loading />;
  }
}
