import React, { Component } from "react";
import { Link } from "react-router-dom";
import Header from "./Header";
import { Loading, scrollTop } from "../Helpers";
import config from "../config";

export default class Page extends Component {
  state = {
    page: null
  };

  componentDidMount() {
    if (config.testing) {
      setTimeout(() => this.getPage(), 2000);
    } else {
      this.getPage();
    }
    scrollTop();
  }

  getPage() {
    fetch(
      `https://public-api.wordpress.com/wp/v2/sites/zubayr.home.blog/pages?_embed&slug=${this.props.match.path.replace(
        "/",
        ""
      )}}`
    )
      .then(res => res.json())
      .then(data => this.setState({ page: data[0] }));
  }

  render() {
    const { page } = this.state;

    if (page) {
      document.title = page.title.rendered;
      return (
        <React.Fragment>
          <Header
            title={page.title.rendered}
            subtitle=""
            image={page._embedded["wp:featuredmedia"][0].source_url}
          />
          <div className="container">
            <div className="row">
              <div className="col-lg-8 col-md-10 mx-auto">
                <div
                  dangerouslySetInnerHTML={{
                    __html: page.content.rendered
                  }}
                />
              </div>
            </div>
          </div>
        </React.Fragment>
      );
    }
    return <Loading />;
  }
}
