import React from "react";
import "../node_modules/startbootstrap-clean-blog/vendor/bootstrap/css/bootstrap.min.css";
import "../node_modules/startbootstrap-clean-blog/vendor/fontawesome-free/css/all.min.css";
import "../node_modules/startbootstrap-clean-blog/css/clean-blog.min.css";
import "../node_modules/startbootstrap-clean-blog/vendor/bootstrap/js/bootstrap.bundle.min.js";

import Navigation from "./Components/Navigation";
import Footer from "./Components/Footer";
import Posts from "./Components/Posts";
import Post from "./Components/Post";
import Page from "./Components/Page";

import { Route, Switch } from "react-router-dom";

function App() {
  return (
    <div>
      <Navigation />
      <Switch>
        <Route path="/" exact component={Posts} />
        <Route path="/about" exact component={Page} />
        <Route path="/:slug" component={Post} />
      </Switch>
      <Footer />
    </div>
  );
}

export default App;
