import React, { Component } from "react";
import { Link } from "react-router-dom";
import Header from "./Header";
import { Loading, scrollTop } from "../Helpers";
import config from "../config";

export default class Posts extends Component {
  state = {
    posts: null
  };

  componentDidMount() {
    if (config.testing) {
      setTimeout(() => this.getPosts(), 2000);
    } else {
      this.getPosts();
    }

    scrollTop();
  }
  getPosts() {
    fetch(
      "https://public-api.wordpress.com/wp/v2/sites/zubayr.home.blog/posts?_embed"
    )
      .then(res => res.json())
      .then(posts => this.setState({ posts }));
  }

  render() {
    const { posts } = this.state;

    if (posts) {
      return (
        <React.Fragment>
          <Header />
          <div className="container">
            <div className="row">
              <div className="col-lg-8 col-md-10 mx-auto">
                {posts.map((post, index) => (
                  <React.Fragment key={index}>
                    <Post post={post} />
                    {index !== posts.length - 1 && <hr />}
                  </React.Fragment>
                ))}
              </div>
            </div>
          </div>
        </React.Fragment>
      );
    }
    return <Loading />;
  }
}

function Post(props) {
  const { post } = props;
  return (
    <div className="post-preview">
      <Link to={`/${post.slug}`}>
        <h2
          className="post-title"
          dangerouslySetInnerHTML={{ __html: post.title.rendered }}
        />
        <h3
          className="post-subtitle"
          dangerouslySetInnerHTML={{
            __html: post.excerpt.rendered.split("<a href")[0]
          }}
        />
      </Link>
      <p className="post-meta">
        Posted by &nbsp;
        <a href="/">{post._embedded.author[0].name}</a>&nbsp; on&nbsp;
        {new Date(post.date).toDateString()}
      </p>
    </div>
  );
}
