import React from "react";
import Header from "./Header";

const Loading = () => {
  return (
    <React.Fragment>
      <Header />
      <div className="container">
        <div className="row">
          <div className="col-lg-8 col-md-10 mx-auto">
            <h2 className="text-center">Loading...</h2>
          </div>
        </div>
      </div>
    </React.Fragment>
  );
};

export default Loading;
